<template>
  <div class="payment-chat">
    <!--  -->
    <div class="chat-icon" @click="toggleChat">
      <i class="fas fa-comments"></i>
      <!--  -->
      <span v-if="unreadCount > 0" class="unread-badge">
        {{ unreadCount }}
      </span>
    </div>

    <!--  -->
    <div v-if="showChat" :class="['chat-window', { closing: isClosing }]">
      <div class="chat-header">
        <div class="header-content">
          <i class="fas fa-headset header-icon"></i>
          <span class="header-title"></span>
        </div>
        <button class="close-btn" @click="closeChat" title="">
          <i class="fas fa-times"></i>
        </button>
      </div>

      <div class="chat-messages" ref="messageContainer">
        <div v-for="msg in messages" :key="msg._id" 
          :class="['message', getMessageClass(msg)]">
          <div class="sender-info" v-if="showSenderInfo(msg)">
            {{ getSenderLabel(msg) }}
          </div>
          <div class="message-content">
            <template v-if="msg.type === 'image'">
              <img 
                :src="getImageUrl(msg.content)" 
                alt="" 
                @click="previewImage = getImageUrl(msg.content)"
                @error="handleImageError"
                class="chat-image"
              >
            </template>
            <template v-else>
              {{ msg.content }}
            </template>
          </div>
          <div class="message-time">{{ formatTime(msg.createdAt) }}</div>
        </div>
      </div>

      <div class="chat-input">
        <!--  -->
        <div class="emoji-picker" v-if="showEmojiPicker">
          <div class="emoji-container">
            <span v-for="emoji in emojis" 
              :key="emoji"
              @click="insertEmoji(emoji)"
              class="emoji-item"
            >{{ emoji }}</span>
          </div>
        </div>

        <!--  -->
        <div class="input-toolbar">
          <button class="tool-btn" @click="toggleEmojiPicker">
            <i class="far fa-smile"></i>
          </button>
          <label class="tool-btn">
            <i class="far fa-image"></i>
            <input 
              type="file"
              accept="image/*"
              @change="handleImageUpload"
              style="display: none"
            >
          </label>
        </div>

        <input 
          v-model="newMessage" 
          @keyup.enter="sendMessage"
          placeholder="..."
          ref="messageInput"
        >
        <button 
          @click.stop="sendMessage()" 
          :disabled="!canSendMessage"
        >
          <i class="fas fa-paper-plane"></i>
        </button>
      </div>
    </div>

    <!--  -->
    <div v-if="previewImage" class="image-preview-modal" @click="closePreview">
      <div class="preview-content" @click.stop>
        <img :src="previewImage" alt="">
        <div class="preview-actions">
          <button class="cancel-btn" @click="closePreview"></button>
          <button v-if="imageFile" class="send-btn" @click="sendImage"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { EventBus } from '@/eventBus'

export default {
  name: 'PaymentChat',
  props: {
    orderId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      showChat: false,
      messages: [],
      newMessage: '',
      unreadCount: 0,
      socketInitialized: false,
      isLoading: false,
      localMessages: [],
      processedMessageIds: new Set(),
      showEmojiPicker: false,
      emojis: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
      uploadingImage: false,
      isClosing: false
    }
  },
  computed: {
    canSendMessage() {
      return !this.uploadingImage && this.newMessage.trim();
    }
  },
  methods: {
    getImageUrl(content) {
      if (!content || content.startsWith('http') || content.startsWith('blob:')) {
        return content;
      }
      const baseUrl = (process.env.VUE_APP_API_BASE_URL || '').replace(/\/$/, '');
      const imagePath = content.startsWith('/') ? content : `/${content}`;
      return `${baseUrl}${imagePath}`;
    },
    toggleChat() {
      if (this.showChat) {
        this.closeChat();
      } else {
        this.showChat = true;
        this.unreadCount = 0;
        EventBus.$emit('payment-chat-toggle', true);
      }
    },
    initSocket() {
      if (this.socketInitialized) return;

      if (!this.$socket) {
        console.error('Socket not initialized');
        return;
      }

      // 
      if (this.orderId) {
        
        // 
        this.$socket.emit('join-chat', { 
          room: `order:${this.orderId}`
        });
        
        // 
        this.$socket.off('newMessage');
        
        // ()
        this.$socket.on('newMessage', (data) => {
          
          if (data.orderId === this.orderId) {
            const message = data.message;
            
            if (!this.processedMessageIds.has(message._id)) {
              this.processedMessageIds.add(message._id);
              this.messages.push(message);
              
              // 
              if (!this.showChat && message.sender === 'merchant') {
                this.unreadCount++;
              }
              
              // 
              this.messages.sort((a, b) => 
                new Date(a.createdAt) - new Date(b.createdAt)
              );
              
              this.$nextTick(() => {
                this.scrollToBottom();
              });
            }
          }
        });

        // 
        this.$socket.on('connect', () => {
          // 
          this.$socket.emit('join-chat', { 
            room: `order:${this.orderId}`
          });
        });

        this.$socket.on('disconnect', () => {
          this.socketInitialized = false;
        });

        this.socketInitialized = true;
      }
    },
    async loadMessages() {
      if (this.isLoading) return;
      
      try {
        this.isLoading = true;
        const response = await this.$api.get(`/api/messages/${this.orderId}`);
        
        // 
        response.data.forEach(msg => {
          if (!this.processedMessageIds.has(msg._id)) {
            this.processedMessageIds.add(msg._id);
            this.messages.push(msg);
          }
        });
        
        // 
        this.messages.sort((a, b) => 
          new Date(a.createdAt) - new Date(b.createdAt)
        );
        
        this.$nextTick(() => {
          this.scrollToBottom();
        });
      } catch (error) {
        console.error('Load messages error:', error);
      } finally {
        this.isLoading = false;
      }
    },
    async sendMessage(imageUrl = null) {
      if ((!this.newMessage.trim() && !imageUrl) || !this.orderId) return;
      
      try {
        const messageData = {
          content: imageUrl || this.newMessage.trim(),
          type: imageUrl ? 'image' : 'text',
          sender: 'user'  // 
        };


        const response = await this.$api.post(`/api/messages/${this.orderId}`, messageData);

        if (response.data) {
          // ()
          if (!imageUrl) {
            this.newMessage = '';
          }

          // 
          if (!this.processedMessageIds.has(response.data._id)) {
            this.processedMessageIds.add(response.data._id);
            this.messages.push(response.data);
            
            // 
            this.messages.sort((a, b) => 
              new Date(a.createdAt) - new Date(b.createdAt)
            );
          }

          this.$nextTick(() => {
            this.scrollToBottom();
          });
        }
      } catch (error) {
        console.error('Send message error:', error);
        this.$message.error('');
      }
    },
    formatTime(date) {
      return new Date(date).toLocaleTimeString();
    },
    scrollToBottom() {
      if (this.$refs.messageContainer) {
        this.$refs.messageContainer.scrollTop = this.$refs.messageContainer.scrollHeight;
      }
    },
    getMessageClass(message) {
      return message.sender === 'user' ? 'sent' : 'received';
    },
    showSenderInfo(message) {
      return message.sender === 'merchant' || message.sender === 'user';
    },
    getSenderLabel(message) {
      return message.sender === 'user' ? '' : '';
    },
    toggleEmojiPicker() {
      this.showEmojiPicker = !this.showEmojiPicker;
    },
    insertEmoji(emoji) {
      this.newMessage += emoji;
      this.$refs.messageInput.focus();
    },
    async handleImageUpload(event) {
      const file = event.target.files[0];
      if (!file) return;
      
      // 
      if (!file.type.startsWith('image/')) {
        this.$message.error('');
        return;
      }
      
      if (file.size > 5 * 1024 * 1024) { // 5MB
        this.$message.error('5MB');
        return;
      }
      
      this.uploadingImage = true;
      try {
        //  FormData
        const formData = new FormData();
        formData.append('image', file);
        
        // 
        const response = await this.$api.post('/api/messages/upload-image', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });
        
        // 
        if (response.data.imageUrl) {
          await this.sendMessage(response.data.imageUrl);
        }
      } catch (error) {
        console.error('Upload image error:', error);
        this.$message.error('');
      } finally {
        this.uploadingImage = false;
      }
    },
    handleImageError(e) {
      console.error('Image load failed:', e.target.src);
      if (!e.target.src.includes('/chat/')) {
        const newSrc = e.target.src.replace('/uploads/', '/uploads/chat/');
        e.target.src = newSrc;
      } else {
        e.target.classList.add('image-error');
        e.target.title = '';
      }
    },
    handleInput(event) {
      this.newMessage = event.target.value;
    },
    handleKeyPress(event) {
      if (event.key === 'Enter' && !event.shiftKey) {
        event.preventDefault();
        this.sendMessage();
      }
    },
    reconnectSocket() {
      if (this.$socket && this.orderId) {
        this.socketInitialized = false;
        this.initSocket();
      }
    },
    closeChat() {
      this.isClosing = true;
      setTimeout(() => {
        this.showChat = false;
        this.isClosing = false;
        EventBus.$emit('payment-chat-toggle', false);
      }, 300);
    }
  },
  mounted() {
    //  socket 
    this.initSocket();
    
    // 
    if (this.orderId) {
      this.loadMessages();
    }

    // 
    if (this.$socket) {
      this.$socket.on('connect', () => {
        this.initSocket();
      });

      this.$socket.on('disconnect', () => {
        this.socketInitialized = false;
      });
    }
  },
  beforeDestroy() {
    if (this.$socket && this.orderId) {
      this.$socket.emit('leave-chat', {
        room: `order:${this.orderId}`
      });
      this.$socket.off('newMessage');
      this.$socket.off('connect');
      this.$socket.off('disconnect');
      this.socketInitialized = false;
    }
  }
}
</script>

