<template>
  <div class="payment-page">

    <!--  -->
    <main class="payment-container" :style="showKeypad && !showChat ? { paddingBottom: '300px' } : {}">
      <!--  -->
      <div v-if="!showKeypad" class="card">
        <div class="card-header">
          <div>
            <div class="merchant-small-title"></div>
            <div class="merchant-name">WAP</div>
          </div>
          <img src="@/assets/img/logo1.png" alt="" class="merchant-avatar">
        </div>
        <div class="card-body">
          <div class="merchant-type"></div>
          <div class="card-body-down">
            <img src="@/assets/cardIcon.svg" alt="card" class="card-icon">
            <div class="merchant-id-text">S1011</div>
          </div>
        </div>
      </div>

      <!--  - / -->
      <div v-if="clientType === 'other' && !showWechatGuide && !disableAllInput" class="payment-method-options payment-method-inline">
        <div 
          class="payment-method-option" 
          :class="{ active: selectedPaymentMethod === 1 }"
          @click="selectPaymentMethod(1)"
        >
          <div class="payment-method-icon wechat">
            <i class="fab fa-weixin"></i>
          </div>
          <div class="payment-method-name"></div>
        </div>
        <div 
          class="payment-method-option" 
          :class="{ active: selectedPaymentMethod === 2 }"
          @click="selectPaymentMethod(2)"
        >
          <div class="payment-method-icon alipay">
            <i class="fab fa-alipay"></i>
          </div>
          <div class="payment-method-name"></div>
        </div>
      </div>

      <!--  -->
      <div class="payment-amount-section">
        <label class="payment-amount-label"></label>
        <div class="amount-input-container" @click.stop="toggleKeypad">
          <span class="currency-symbol"></span>
          <input 
            type="text" 
            class="amount-input" 
            :value="formattedAmount"
            readonly
            :disabled="disableAllInput || showWechatGuide"
            @click.stop="toggleKeypad"
          >
        </div>
      </div>

      <!--  -->
      <button 
        v-if="!showKeypad"
        class="pay-button"
        :class="{ active: isValidAmount && !error, loading: processing }"
        :disabled="!isValidAmount || error || processing || showWechatGuide || (clientType === 'other' && selectedPaymentMethod === null) || isPreRunning"
        @click="processPayment"
      >
        <template v-if="!processing && !isPreRunning">
          <span class="pay-text"></span>
        </template>
        <template v-else-if="isPreRunning">
          <div class="spinner-wrapper">
            <div class="button-spinner"></div>
            <span>...</span>
          </div>
        </template>
        <template v-else>
          <div class="spinner-wrapper">
            <div class="button-spinner"></div>
            <span>...</span>
          </div>
        </template>
      </button>

      <!--  -->
      <div class="keypad" @click.stop v-show="showKeypad && !showChat">
        <div class="keypad-container">
          <!--  -->
          <div class="keypad-numbers">
            <!-- 1 2 3 -->
          <button 
            type="button" 
            class="keypad-button"
              @click.stop="inputNumber(1)"
            :disabled="disableAllInput || showWechatGuide"
            >1</button>
          <button 
            type="button" 
            class="keypad-button"
              @click.stop="inputNumber(2)"
            :disabled="disableAllInput || showWechatGuide"
            >2</button>
          <button 
            type="button" 
            class="keypad-button"
              @click.stop="inputNumber(3)"
              :disabled="disableAllInput || showWechatGuide"
            >3</button>
            
            <!-- 4 5 6 -->
            <button 
              type="button" 
              class="keypad-button"
              @click.stop="inputNumber(4)"
              :disabled="disableAllInput || showWechatGuide"
            >4</button>
            <button 
              type="button" 
              class="keypad-button"
              @click.stop="inputNumber(5)"
              :disabled="disableAllInput || showWechatGuide"
            >5</button>
            <button 
              type="button" 
              class="keypad-button"
              @click.stop="inputNumber(6)"
              :disabled="disableAllInput || showWechatGuide"
            >6</button>
            
            <!-- 7 8 9 -->
            <button 
              type="button" 
              class="keypad-button"
              @click.stop="inputNumber(7)"
              :disabled="disableAllInput || showWechatGuide"
            >7</button>
            <button 
              type="button" 
              class="keypad-button"
              @click.stop="inputNumber(8)"
              :disabled="disableAllInput || showWechatGuide"
            >8</button>
            <button 
              type="button" 
              class="keypad-button"
              @click.stop="inputNumber(9)"
              :disabled="disableAllInput || showWechatGuide"
            >9</button>
            
            <!-- 0()  -->
            <button 
              type="button" 
              class="keypad-button zero-button"
            @click.stop="inputNumber(0)"
            :disabled="disableAllInput || showWechatGuide"
          >0</button>
            <button 
              type="button" 
              class="keypad-button"
              @click.stop="inputDot"
              :disabled="disableAllInput || showWechatGuide"
            >.</button>
          </div>
          
          <!--  -->
          <div class="keypad-actions">
            <!--  -->
          <button 
            type="button" 
            class="keypad-button delete-button"
            @click.stop="deleteNumber"
            :disabled="disableAllInput || showWechatGuide"
          >
            <svg xmlns="http://www.w3.org/2000/svg" width="29" height="18.82" viewBox="0 0 29 18.82">
              <path class="delete-icon-path" d="M9.41,18.82a1.807,1.807,0,0,1-1.246-.5L.807,11.2A2.274,2.274,0,0,1,0,9.415a2.209,2.209,0,0,1,.8-1.78L8.164.5A1.807,1.807,0,0,1,9.41,0H25.39A3.522,3.522,0,0,1,29,3.411v12a3.517,3.517,0,0,1-3.607,3.411ZM2.03,8.794a.776.776,0,0,0-.283.624.806.806,0,0,0,.283.624L9.4,17.171H25.39a1.8,1.8,0,0,0,1.846-1.746V3.411A1.8,1.8,0,0,0,25.39,1.665H9.4Zm18.3,4-3.343-2.344-3.342,2.342a1.226,1.226,0,0,1-1.355,0,.533.533,0,0,1,0-.951L15.634,9.5,12.292,7.155A.6.6,0,0,1,12,6.68a.6.6,0,0,1,.292-.475,1.226,1.226,0,0,1,1.355,0l3.358,2.344L20.351,6.2a1.226,1.226,0,0,1,1.355,0,.541.541,0,0,1,0,.964L18.361,9.512l3.326,2.333a.533.533,0,0,1,0,.95,1.226,1.226,0,0,1-1.355,0Z"/>
            </svg>
          </button>
            
            <!--  -->
            <button 
              type="button" 
              class="confirm-pay-button"
              @click.stop="processPayment"
              :disabled="!isValidAmount || error || processing || showWechatGuide || (clientType === 'other' && selectedPaymentMethod === null) || isPreRunning"
            >
              <template v-if="!isPreRunning"></template>
              <template v-else>...</template>
            </button>
          </div>
        </div>
      </div>

      <!--  -->
      <div v-if="error" class="error-message">
        {{ error }}
      </div>

      <!--  -->
      <div v-if="loading" class="loading-overlay">
        <div class="loading-container">
          <div class="loading-spinner"></div>
          <span class="loading-text">...</span>
        </div>
      </div>


      <!--  -->
      <div v-if="processing" class="progress-overlay">
        <div class="progress-container">
          <div class="progress-bar">
            <div class="progress-fill" :style="{ width: progressWidth + '%' }"></div>
          </div>
          <div class="progress-text">
            {{ progressText }}
          </div>
        </div>
      </div>

      <!--  -->
      <div 
        v-if="showAnnouncement && channelSettings?.announcement?.enabled" 
        class="announcement-overlay"
      >
        <div class="announcement-modal" @click.stop>
          <div class="announcement-header">
            <div class="header-icon">
              <i class="fas fa-bullhorn"></i>
            </div>
            <h3></h3>
            <button class="close-btn" @click="closeAnnouncement">
              <i class="fas fa-times"></i>
            </button>
          </div>
          <div class="announcement-content">
            {{ channelSettings.announcement.content }}
          </div>
          <div class="announcement-footer">
            <button class="confirm-btn" @click="closeAnnouncement">
              <i class="fas fa-check"></i>
              <span></span>
            </button>
          </div>
        </div>
      </div>

      <!--  -->
      <PaymentChat 
        v-if="shouldShowChat"
        :orderId="initialOrder.orderId"
        :key="initialOrder.orderId"
      />

      <!--  -->
      <div v-if="showNameInput" class="modal-overlay">
        <div class="name-input-modal">
          <h3></h3>
          <div class="input-group">
            <input 
              type="text" 
              v-model="customerName"
              placeholder=""
              maxlength="20"
              @keyup.enter="confirmName"
            >
            <p class="error-text" v-if="nameError">{{ nameError }}</p>
          </div>
          <div class="modal-buttons">
            <button 
              class="cancel-btn" 
              @click="cancelNameInput"
            ></button>
            <button 
              class="confirm-btn" 
              @click="confirmName"
              :disabled="!isValidName"
            ></button>
          </div>
        </div>
      </div>

      <!--  -->
      <div v-if="showPaymentQRModal" class="modal-overlay" @click.stop>
        <div class="payment-link-modal">
          <div class="modal-header">
            <h3></h3>
            <!--  -->
            <!-- 
            <button class="close-btn" @click="closePaymentLinkModal">
              <i class="fas fa-times"></i>
            </button> 
            -->
          </div>
          <div class="modal-content">
            <p class="modal-info"></p>
            
            <!--  -->
            <div class="qr-code-container">
              <div v-if="qrCodeUrl" class="qr-wrapper">
                <!-- canvas -->
                <canvas ref="qrCanvas" class="qr-canvas" style="display: none;"></canvas>
                <!--  -->
                <img v-if="qrImageSrc" :src="qrImageSrc" class="qr-image" alt="" />
                <div v-else class="qr-loading">...</div>
              </div>
            </div>
            
            <p class="modal-secondary-info"></p>
            
            <div class="payment-link-container">
              <p class="payment-link">{{ qrCodeText }}</p>
              <button class="copy-btn" @click="copyPaymentLink">
                <i class="fas fa-copy"></i> 
              </button>
            </div>
          </div>
          <!--  -->
          <!-- 
          <div class="modal-footer">
            <button class="confirm-btn" @click="closePaymentLinkModal">
              <span></span>
            </button>
          </div> 
          -->
        </div>
      </div>
    </main>

    <!--  (SVG) -->
    <div v-if="showWechatGuide" class="wechat-guide-overlay-svg">
      <div class="wechat-guide-content-svg">
        <!-- SVG  -->
        <svg class="guide-arrow-svg-curved" width="120" height="120" viewBox="0 0 120 120" fill="none" xmlns="http://www.w3.org/2000/svg">
          <!--  -->
          <path d="M90 20 L100 30" stroke="white" stroke-width="6" stroke-linecap="round"/>
          <path d="M90 20 L80 30" stroke="white" stroke-width="6" stroke-linecap="round"/>
          <!--  -->
          <path d="M90 30 Q 60 100, 3 100" stroke="white" stroke-width="6" stroke-linecap="round" stroke-dasharray="10 8" fill="none"/>
        </svg>
        <p class="guide-text-svg"> <span class="dots-svg">...</span><br><span class="highlight-svg"></span></p>
      </div>
    </div>
  </div>
</template>

<script>
import { EventBus } from '@/eventBus'
import PaymentChat from '@/components/PaymentChat.vue'

export default {
  name: 'PaymentPage',
  components: {
    PaymentChat
  },
  data() {
    return {
      amount: '',
      channelId: '',
      userId: '',
      channelSettings: null,
      error: null,
      loading: true,
      ipAddress: null,
      disableAllInput: false, // Reverted default to false
      qrCodeUrl: null,
      clientType: null,
      paymentType: null,
      processing: false,
      progressWidth: 0,
      progressText: '...',
      progressTimer: null,
      showAnnouncement: false,
      initialOrder: null,
      orderUpdating: false,
      showChat: false,
      showNameInput: false,
      customerName: '',
      nameError: '',
      pendingOrder: null,
      paymentTimer: null,
      hasReceivedUrl: false,
      proxyInfo: null,
      showPaymentQRModal: false,
      qrCodeText: null,
      qrImageSrc: null,  // 
      isWechatBrowser: false,  // 
      showWechatGuide: false,  // 
      showKeypad: false,
      selectedPaymentMethod: null, // 
      hxzfScriptRunning: false, // hxzf
      hxzfScriptId: null, // hxzfID
      oldHxzfScriptRunning: false, // old_hxzf
      oldHxzfScriptId: null, // old_hxzfID
      ipLocation: null, // : IP
      isPreRunning: false, // 
    }
  },
  computed: {
    isValidAmount() {
      return this.amount && parseFloat(this.amount) > 0
    },
    formattedAmount() {
      if (this.amount === '') return '0.00'
      const parsedAmount = parseFloat(this.amount)
      return isNaN(parsedAmount) ? '0.00' : parsedAmount.toFixed(2)
    },
    shouldShowChat() {
      const shouldShow = !!(this.initialOrder && this.initialOrder.orderId && this.channelSettings?.chatFeature?.enabled);
      return shouldShow;
    },
    isValidName() {
      return this.customerName.trim().length >= 2;
    }
  },
  methods: {
    // 
    async selectPaymentMethod(methodType) {
      this.selectedPaymentMethod = methodType;
      this.paymentType = methodType;
      
      // 
      await this.triggerPreRunIfNeeded();
    },
    
    async loadChannelSettings() {
      try {
        const response = await this.$api.get(`/api/channels/settings/${this.channelId}`, {
          params: { userId: this.userId }
        });
        
        this.channelSettings = response.data;
        
        // 
        if (this.channelSettings.announcement?.enabled) {
          this.showAnnouncement = true;
        }

        // 
        this.isWechatBrowser = this.isWechatBrowserFunc();
        
        //  f2f
        if (this.isWechatBrowser && 
            this.channelSettings.identifier === 'f2f' && 
            this.channelSettings.customParams?.wechatGuideEnabled === true) {
          this.showWechatGuide = true;
        }
        
        //  chatFeature 
        this.channelSettings = {
          ...response.data,
          chatFeature: response.data.chatFeature || {
            enabled: false
          }
        };
        
        // 
        if (this.channelSettings.status !== '') {
          this.error = '';
          this.disableAllInput = true;
          return;
        }

        // 
        if (!['ZFBZZ', 'ckxa', 'xlr', '98pay', 'wubai', 'xfzf', 'wfb', 'jtpay', 'hxzf', 'old_hxzf', '5zf', 'ydd', '003', 'lilipay', 'heishi', 'yilian', 'ysm', 'f2f', '213pay', 'golden', 'wxtoqq', 'hkrt', '76pay'].includes(this.channelSettings.identifier)) {
          this.error = '';
          this.disableAllInput = true;
          return;
        }

        // 
        if (!this.channelSettings.customParams?.userId) {
          // 21lili,merchantIdkey
          if (this.channelSettings.identifier === 'lilipay') {
            if (!this.channelSettings.customParams?.merchantId || !this.channelSettings.customParams?.key) {
              this.error = 'ID';
              this.disableAllInput = true;
              return;
            }
          } else if (this.channelSettings.identifier === 'ysm') { // 
            if (!this.channelSettings.customParams?.appid || !this.channelSettings.customParams?.appSecret) {
              this.error = 'appidappSecret';
              this.disableAllInput = true;
              return;
            }
          } else if (this.channelSettings.identifier === 'hkrt') { // 
            if (!this.channelSettings.customParams?.token) {
              this.error = 'Token';
              this.disableAllInput = true;
              return;
            }
          } else {
            this.error = '';
            this.disableAllInput = true;
            return;
          }
        }

        // ****
        if (this.channelSettings.nameRequired?.enabled) {
          this.showNameInput = true;
        }

        // 76
        if (this.channelSettings.identifier === '76pay') {
          await this.runProxyScript();
        }

      } catch (error) {
        console.error('Load channel settings error:', error);
        if (error.response?.status === 404) {
          this.error = '';
        } else {
          this.error = error.response?.data?.message || '';
        }
        this.disableAllInput = true;
      }
    },
    async getClientIP() {
      try {
        const response = await this.$api.get('/api/ip');
        return response.data?.ip || '0.0.0.0';
      } catch (error) {
        console.error('Get IP error:', error);
        return '0.0.0.0';
      }
    },
    async checkIPBlocked() {
      this.ipLocation = null; 
      if (!this.ipAddress) return false;
      
      try {
        const response = await this.$api.get('/api/orders/check-ip', {
          params: { 
            ipAddress: this.ipAddress,
            userId: this.userId,
            channelId: this.channelId
          }
        });
        
        // IP
        if (response.data.ipLocation) {
          this.ipLocation = response.data.ipLocation;
          console.log('IP:', this.ipLocation); // 
        }
        
        // IP
        if (response.data.isInternal) {
          return false;
        }
        
        // IP
        if (response.data.blocked) {
          let message = 'IP';
          if (response.data.reason) {
            message += `\n: ${response.data.reason}`;
          }
          if (response.data.blockedBy && response.data.sharedBlacklist) {
            message += `\n()`;
          }
          this.error = message;
          this.disableAllInput = true;
          this.amount = '';
          this.disablePayment = true;
          return true;
        }

        // 
        if (response.data.ipLocation && this.channelSettings?.regionBlacklist?.enabled) {
          const { province, city } = response.data.ipLocation;
          const { provinces, cities } = this.channelSettings.regionBlacklist;
          
          // 
          if (provinces.includes(province)) {
            this.error = '';
            this.disableAllInput = true;
            this.amount = '';
            this.disablePayment = true;
            return true;
          }
          
          // 
          if (cities && cities.length > 0) {
            if (cities.includes(city)) {
              this.error = '';
              this.disableAllInput = true;
              this.amount = '';
              this.disablePayment = true;
              return true;
            }
          } else {
            // 
            if (provinces.includes(province)) {
              this.error = '';
              this.disableAllInput = true;
              this.amount = '';
              this.disablePayment = true;
              return true;
            }
          }
        }

        return false;
      } catch (error) {
        console.error('Check IP error:', error);
        // ,
        return false;
      }
    },
    detectClient() {
      const ua = navigator.userAgent.toLowerCase();
      
      if (/micromessenger|weixin|wechat/i.test(ua)) {
        this.clientType = 'wechat';
        this.paymentType = 1;  // 
      } else if (/alipayclient/i.test(ua)) {
        this.clientType = 'alipay';
        this.paymentType = 2;  // 
      } else {
        this.clientType = 'other';
        
        //  f2f 
        if (this.channelSettings?.identifier !== 'f2f' && 
            this.channelSettings?.customParams?.disableOutsideBrowser === true) {
          this.error = '';
          this.disableAllInput = true;
        } else {
          // 
          this.paymentType = null;
        }
      }
    },
    closeAnnouncement() {
      this.showAnnouncement = false;
    },
    inputNumber(num) {
      if (this.disableAllInput) return;
      if (this.amount.includes('.') && this.amount.split('.')[1].length >= 2) return;
      if (this.amount.length >= 9) return;
      if (this.amount === '0' && num !== 0) {
        this.amount = num.toString();
      } else {
        this.amount += num.toString();
      }
    },
    inputDot() {
      if (this.disableAllInput) return;
      if (this.amount === '') {
        this.amount = '0.';
      } else if (!this.amount.includes('.')) {
        this.amount += '.';
      }
    },
    deleteNumber() {
      if (this.disableAllInput) return;
      this.amount = this.amount.slice(0, -1);
    },
    async processPayment() {
      if (!this.isValidAmount || this.disableAllInput || this.processing || this.isPreRunning) return;
      
      // /
      if (this.clientType === 'other' && this.selectedPaymentMethod === null) {
        this.error = '';
        return;
      }

      // hxzfold_hxzf
      await this.triggerPreRunIfNeeded();
      
      // 
      if (this.isPreRunning) return;

      try {
        this.processing = true;
        this.error = null;
        
        //  IP 
        // IP created() 
        // IP
        // const isBlocked = await this.checkIPBlocked();
        // if (isBlocked) {
        //   this.error = ' IP ,';
        //   this.disableAllInput = true;
        //   this.stopProgressBar(false);
        //   return;
        // }

        this.startProgressBar();

        // other
        if (this.clientType === 'other' && !this.selectedPaymentMethod) {
          this.error = '';
          this.stopProgressBar(false); // 
          return;
        }

        // WebSocket
        if (this.$socket && this.initialOrder) {
          try {
            // newOrder
            const orderData = {
              ...this.initialOrder,
              amount: parseFloat(this.amount),   // 
              originalAmount: parseFloat(this.amount),
              status: "pending",
              amountUpdatedAt: new Date().toISOString()
            };
            
            // newOrder
            this.$socket.emit("newOrder", orderData);
          } catch (error) {
            console.error('newOrder WebSocket:', error);
            // 
          }
        }

        // 
        const response = await this.$api.put(
          `/api/orders/${this.initialOrder.orderId}/amount`,
          { 
            amount: parseFloat(this.amount),
            originalAmount: parseFloat(this.amount) // 
          },
          { skipAuthHeader: true }
        );

        // 
        let paymentResponse;
        try {
          switch (this.channelSettings.identifier) {
            case 'ckxa':
              paymentResponse = await this.handleCkxaPayment(response.data.order);
              break;
            case 'xlr':
              paymentResponse = await this.handleXlrPayment(response.data.order);
              break;
            case '98pay':
              try {
                paymentResponse = await this.handle98Payment(response.data.order);
                // 
                if (!paymentResponse?.data?.payUrl) {
                  console.error('Invalid 98pay response:', paymentResponse);
                  throw new Error('');
                }
                // 
                const payUrl = paymentResponse.data.payUrl;
                if (!payUrl.startsWith('http')) {
                  console.error('Invalid pay URL format:', payUrl);
                  throw new Error('');
                }
              } catch (error) {
                console.error('98pay payment failed:', error);
                throw new Error(error.message || '');
              }
              break;
            case 'ZFBZZ':
              if (this.clientType !== 'alipay') {
                throw new Error('');
              }
              paymentResponse = await this.handleZfbzzPayment(response.data.order);
              break;
            case 'wubai':
              paymentResponse = await this.handleWubaiPayment(response.data.order);
              break;
            case 'xfzf':
              paymentResponse = await this.handleXfzfPayment(response.data.order);
              break;
            case 'wfb':
              paymentResponse = await this.handleWfbPayment(response.data.order);
              break;
            case 'jtpay':
              paymentResponse = await this.handleJtpayPayment(response.data.order);
              break;
            case 'hxzf':
              paymentResponse = await this.handleHxzfPayment(response.data.order);
              break;
            case 'old_hxzf':
              paymentResponse = await this.handleOldHxzfPayment(response.data.order);
              break;
            case '5zf':
              paymentResponse = await this.handle5zfPayment(response.data.order);
              break;
            case 'ydd':  // 
              paymentResponse = await this.handleYddPayment(response.data.order);
              break;
            case '003':  // 003
              paymentResponse = await this.handle003Payment(response.data.order);
              break;
            case 'lilipay':
              paymentResponse = await this.handleLiliPayment(response.data.order);
              break;
            case 'heishi':
              paymentResponse = await this.handleHeishiPayment(response.data.order);
              break;
            case 'yilian':
              paymentResponse = await this.handleYilianPayment(response.data.order);
              break;
            case 'ysm': // 
              paymentResponse = await this.handleYsmPayment(response.data.order);
              break;
            case 'f2f':
              // 
              this.startProgressBar(60); // 20
              await new Promise(resolve => setTimeout(resolve, 60000)); // 20
              
              paymentResponse = await this.handleF2fPayment(response.data.order);
              break;
            case '213pay':
              paymentResponse = await this.handle213Payment(response.data.order);
              break;
            case 'golden':
              paymentResponse = await this.handleGoldenPayment(response.data.order);
              break;
            case 'wxtoqq':
              paymentResponse = await this.handleWxToQqPayment(response.data.order);
              break;
            case 'hkrt':
              paymentResponse = await this.handleHkrtPayment(response.data.order);
              break;
            case '76pay':
              paymentResponse = await this.handle76Payment(response.data.order);
              break;
            default:
              throw new Error(`: ${this.channelSettings.identifier}`);
          }

          // 
          const finalPayUrl = paymentResponse.data.payUrl;
          if (!finalPayUrl) {
            throw new Error('');
          }

          // httpalipaysweixindata URI
          const isValidUrl = finalPayUrl.startsWith('http') || 
                            finalPayUrl.startsWith('alipays://') || 
                            finalPayUrl.startsWith('weixin://') ||
                            finalPayUrl.startsWith('data:text/html');

          if (!isValidUrl) {
            throw new Error('');
          }

          // 
          this.stopProgressBar(true);
          await this.notifyMerchantAfterScan();
          this.error = null;

          //  setTimeout 
          setTimeout(() => {
            // data URIHTML
            if (finalPayUrl.startsWith('data:text/html')) {
              // HTMLiframe
              const newWindow = window.open('', '_blank');
              if (newWindow) {
                newWindow.document.write(decodeURIComponent(finalPayUrl.split(',')[1]));
                newWindow.document.close();
              } else {
                // 
                document.open();
                document.write(decodeURIComponent(finalPayUrl.split(',')[1]));
                document.close();
              }
            } else if (this.clientType === 'other' && this.selectedPaymentMethod === 1) {
              // /
              this.showPaymentQRModal = true;
              this.qrCodeUrl = finalPayUrl;
              this.qrCodeText = finalPayUrl;
              // 
              this.$notify({
                title: '',
                message: '',
                type: 'info',
                duration: 5000,
                showClose: true
              });
            } else {
              // 
              window.location.href = finalPayUrl;
            }
          }, 100);

        } catch (error) {
          console.error('Payment processing error:', error);
          this.stopProgressBar(false);
          throw error;
        }

      } catch (error) {
        console.error(':', error);
        this.error = error.response?.data?.message || error.message || '';
        this.stopProgressBar(false);
      }
    },
    startProgressBar() {
      this.progressWidth = 0;
      this.progressText = '...';
      
      // 30%
      const quickProgress = setInterval(() => {
        if (this.progressWidth < 30) {
          this.progressWidth += 2; // 
        } else {
          clearInterval(quickProgress);
          // 
          this.startSlowProgress();
        }
      }, 30);
    },
    startSlowProgress() {
      this.progressTimer = setInterval(() => {
        if (this.progressWidth < 85) {
          // 
          if (this.progressWidth < 50) {
            this.progressWidth += 0.4;
            this.progressText = '...';
          } else if (this.progressWidth < 70) {
            this.progressWidth += 0.3;
            this.progressText = '...';
          } else {
            this.progressWidth += 0.2;
            this.progressText = '...';
          }
        }
      }, 100);
    },
    stopProgressBar(success = true) {
      clearInterval(this.progressTimer);
      
      if (success) {
        // 
        const completeProgress = setInterval(() => {
          if (this.progressWidth < 100) {
            this.progressWidth += 3;
          } else {
            clearInterval(completeProgress);
            this.progressText = '!';
            this.processing = false;
          }
        }, 20);
      } else {
        this.progressText = ',';
        this.processing = false;
      }
    },
