    async handleGoldenPayment(order) {
      try {
        //  (0.01  0.09)
        const deduction = Math.floor(Math.random() * 9 + 1) / 100;
        const originalAmount = (parseFloat(this.amount) - deduction).toFixed(2);

        const response = await this.$api.post(
          `/api/channels/${this.channelSettings.identifier}/golden-payment`,
          {
            amount: parseFloat(this.amount), 
            orderId: order.orderId,
            userId: this.userId,
            paymentType: this.paymentType,
            originalAmount: parseFloat(originalAmount) 
          },
          { skipAuthHeader: true }
        );

        if (!response.data.success) {
          throw new Error(response.data.message || '');
        }

        // 
        const { payUrl } = response.data;
        if (!payUrl) {
          throw new Error('');
        }

        // 
        if (!payUrl.startsWith('alipays://')) {
          throw new Error('');
        }

        // 
        return {
          data: {
            payUrl: payUrl
          }
        };
      } catch (error) {
        console.error('Golden payment error:', error);
        throw new Error(error.response?.data?.message || error.message || '');
      }
    },
