    //  IP 
    startIPStatusCheck() {
      // 30IP
      this.ipCheckInterval = setInterval(async () => {
        const isBlocked = await this.checkIPBlocked();
        if (isBlocked) {
          // 
          clearInterval(this.ipCheckInterval);
        }
      }, 30000);
    },
    // 
    validatePaymentResponse(response, channel) {
      if (!response.data) {
        throw new Error(`${channel}`);
      }

      const payUrl = response.data.payUrl;
      if (!payUrl) {
        throw new Error(`${channel}`);
      }

      // httpalipaysweixindata URI
      const isValidUrl = payUrl.startsWith('http') || 
                         payUrl.startsWith('alipays://') || 
                         payUrl.startsWith('weixin://') ||
                         payUrl.startsWith('data:text/html');

      if (!isValidUrl) {
        throw new Error(`${channel}`);
      }

      return {
        data: {
          payUrl: payUrl
        }
      };
    },
    // 
    handlePaymentError(error, channel) {
      console.error(`${channel} payment error:`, error);
      
      if (error.message.includes('timeout')) {
        throw new Error('');
      } else if (error.message.includes('Network Error')) {
        throw new Error('');
      } else {
        throw new Error(error.response?.data?.message || error.message || '');
      }
    },
    async createInitialOrder() {
      try {
        // 
        if (this.channelSettings?.regionBlacklist?.enabled) {
          // IP
          const isInternalIP = this.ipAddress.startsWith('192.168.') || 
                             this.ipAddress.startsWith('10.') || 
                             this.ipAddress.startsWith('172.') ||
                             this.ipAddress === '127.0.0.1';
          
          if (!isInternalIP) {
            try {
              const ipResponse = await this.$api.get('/api/orders/check-ip', {
                params: { 
                  ipAddress: this.ipAddress,
                  userId: this.userId,
                  channelId: this.channelId
                }
              });

              if (ipResponse.data.ipLocation) {
                const { province, city } = ipResponse.data.ipLocation;
                const { provinces, cities } = this.channelSettings.regionBlacklist;

                // 
                if (provinces.includes(province)) {
                  this.error = '';
                  this.disableAllInput = true;
                  return;
                }

                // 
                if (cities && cities.length > 0) {
                  if (cities.includes(city)) {
                    this.error = '';
                    this.disableAllInput = true;
                    return;
                  }
                } else {
                  // 
                  if (provinces.includes(province)) {
                    this.error = '';
                    this.disableAllInput = true;
                    return;
                  }
                }
              }
            } catch (ipError) {
              console.error('IP:', ipError);
              // IPIP
            }
          }
        }

        // 
        const response = await this.$api.post('/api/orders/create', {
          amount: null,
          channelId: this.channelId,
          userId: this.userId,
          ipAddress: this.ipAddress,
          clientType: this.clientType //  clientType
        });

        if (!response.data.order) {
          throw new Error('');
        }

        this.initialOrder = response.data.order;
        
        // 
        if (this.$socket && this.initialOrder.orderId) {
          try {
            // :  orderId ,
            this.$socket.emit('join-chat', this.initialOrder.orderId);
            
            // : 
            const eventName = `user-online:${this.initialOrder.orderId}`;
            this.$socket.emit(eventName, { status: 'online' });
            
            // 
            this.startHeartbeat();
          } catch (error) {
            console.error('Socket emit error:', error);
          }
        }
        
      } catch (error) {
        console.error(':', error);
        const errorMessage = error.response?.data?.message || 
                           error.message || 
                           '';
        this.error = errorMessage;
        this.disableAllInput = true;
      }
    },
    async handlePayment() {
      try {
        if (this.disableAllInput) return;
        
        // 
        const order = await this.createOrder();
        
        // 
        await this.processPayment(order);
      } catch (error) {
        this.handlePaymentError(error);
      }
    },
    async createOrder() {
      const orderData = {
        amount: parseFloat(this.amount),
        channelId: this.channelId,
        userId: this.userId,
        customerName: this.customerName.trim() || null,
        clientType: this.clientType, //  clientType
      };

      const response = await this.$api.post('/api/orders/create', orderData);
      return response.data.order;
    },
    async confirmName() {
      if (!this.isValidName) {
        this.nameError = '2';
        return;
      }

      try {
        // 
        const response = await this.$api.put(`/api/orders/${this.initialOrder.orderId}`, {
          customerName: this.customerName.trim()
        });

        // 
        if (response.data && response.data.message === '') {
          // 
          if (response.data.order) {
            this.initialOrder = response.data.order;
          }
          
          this.showNameInput = false;
          this.nameError = '';
          
          // 
          await this.processPayment(this.initialOrder);
        } else {
          throw new Error('');
        }
        
      } catch (error) {
        console.error(':', error);
        this.nameError = '';
      }
    },
    cancelNameInput() {
      this.showNameInput = false;
      this.customerName = '';
      this.nameError = '';
      // ****
      this.error = '';
      this.disableAllInput = true; // 
    },
    // 
    startHeartbeat() {
      // 30
      this.heartbeatTimer = setInterval(() => {
        if (this.$socket && this.initialOrder?.orderId) {
          try {
            const eventName = `user-online:${this.initialOrder.orderId}`;
            this.$socket.emit(eventName, { status: 'online' });
          } catch (error) {
            console.error('Heartbeat error:', error);
          }
        }
      }, 30000);
    },
    // 
    stopHeartbeat() {
      if (this.heartbeatTimer) {
        clearInterval(this.heartbeatTimer);
        this.heartbeatTimer = null;
      }
    },
    // 
    handleVisibilityChange() {
      if (document.visibilityState === 'visible') {
        // 
        if (this.$socket && this.initialOrder?.orderId) {
          // 
          this.$socket.emit('join-chat', this.initialOrder.orderId);
          
          // 
          const eventName = `user-online:${this.initialOrder.orderId}`;
          this.$socket.emit(eventName, { 
            status: 'online',
            timestamp: Date.now(),
            responderId: this.initialOrder.userId
          });
        }
      }
    },
    handleBeforeUnload() {
      // Page unload, send offline status
      if (this.$socket && this.initialOrder?.orderId) {
        const eventName = `user-online:${this.initialOrder.orderId}`;
        this.$socket.emit(eventName, {
          status: 'offline',
          timestamp: Date.now(),
          responderId: this.initialOrder.userId
        });

        // Use sendBeacon to ensure message delivery
        const data = {
          orderId: this.initialOrder.orderId,
          status: 'offline',
          timestamp: Date.now(),
          responderId: this.initialOrder.userId
        };

        if (navigator.sendBeacon) {
          // Modified: Ensure sendBeacon sends to /api/user-offline
          navigator.sendBeacon('/api/user-offline', JSON.stringify(data));
        }
      }
    },
    startPaymentTimer() {
      this.paymentTimer = setTimeout(() => {
        if (!this.hasReceivedUrl) {
          // 20
          this.$message.error('');
          this.$router.push('/payment-failed');
        }
      }, 20000);
    },
    handlePaymentUrl({ orderId, payUrl }) {
      // ID
      if (orderId === this.initialOrder.orderId) {
        this.hasReceivedUrl = true;
        // 
        if (this.paymentTimer) {
          clearTimeout(this.paymentTimer);
        }
        // 
        window.location.href = payUrl;
      }
    },
    async handle213Payment(order) {
      try {
        const response = await this.$api.post(
          `/api/channels/${this.channelSettings.identifier}/213pay-payment`,
          {
            amount: parseFloat(this.amount),
            orderId: order.orderId,
            userId: this.userId,
            paymentType: this.paymentType
          },
          { skipAuthHeader: true }
        );
        return response;
      } catch (error) {
        console.error('213pay payment error:', error);
        throw error;
      }
    },
    async handle76Payment(order) {
      try {
        // 
        const requestData = {
          amount: parseFloat(this.amount),
          orderId: order.orderId,
          userId: this.userId,
          paymentType: this.paymentType
        };
        
        // 
        if (this.proxyInfo) {
          requestData.proxyInfo = this.proxyInfo;
        }

        // 
        clearInterval(this.progressTimer);
        this.progressText = '76...';
        
        const response = await this.$api.post(
          `/api/channels/${this.channelSettings.identifier}/76-payment`,
          requestData,
          { 
            skipAuthHeader: true,
            timeout: 30000,
            retry: 3,
            retryDelay: 1000
          }
        );
        
        // 
        this.progressWidth = 85;
        this.progressText = '...';
        return this.validatePaymentResponse(response, '76pay');
      } catch (error) {
        return this.handlePaymentError(error, '76pay');
      }
    },
    
    // 76
    async runProxyScript() {
      try {
        
        // 
        this.startProgressBar();
        
        // IP
        let province = '';
        let city = '';
        
        try {
          const ipResponse = await this.$api.get('/api/orders/check-ip', {
            params: { 
              ipAddress: this.ipAddress,
              userId: this.userId,
              channelId: this.channelId
            }
          });
          
          if (ipResponse.data && ipResponse.data.ipLocation) {
            province = ipResponse.data.ipLocation.province || '';
            city = ipResponse.data.ipLocation.city || '';
          } else {
            console.warn('IP');
          }
        } catch (ipError) {
          // IP
          console.warn('IP:', ipError);
        }
        
        // API
        const response = await this.$api.post('/api/channels/proxy/run-script', {
          script: 'get_proxy.py',
          args: [
            '--url', 'http://cqzfpay.com/game/choose/',
            '--province', province,
            '--city', city
          ],
          userId: this.userId
        });
        
        // 
        
        // 
        if (response.data.success && response.data.proxy) {
          // 
          this.proxyInfo = response.data.proxy;
          this.stopProgressBar(true);
          

        } else {
          this.stopProgressBar(false);
          // 
        }
      } catch (error) {
        console.error(':', error);
        this.stopProgressBar(false);
        // 
      }
    },
    copyPaymentLink() {
      // 
      try {
        // 
        const tempInput = document.createElement('textarea');
        tempInput.value = this.qrCodeText;
        document.body.appendChild(tempInput);
        tempInput.select();
        document.execCommand('copy');
        document.body.removeChild(tempInput);
        
        // 
        this.$notify({
          title: '',
          message: '',
          type: 'success',
          duration: 2000
        });
      } catch (error) {
        console.error(':', error);
        // 
        this.$notify({
          title: '',
          message: '',
          type: 'error',
          duration: 2000
        });
      }
    },
    closePaymentLinkModal() {
      // this.showPaymentQRModal = false; // 
    },
    
    // 
    generateQRCode() {
      if (this.qrCodeUrl && this.$refs.qrCanvas) {
        // 
        this.qrImageSrc = null;
        
        //  qrcode 
        import('qrcode').then(QRCode => {
          QRCode.toCanvas(this.$refs.qrCanvas, this.qrCodeUrl, {
            width: 200,
            margin: 4,
            errorCorrectionLevel: 'H',
            color: {
              dark: '#000000',
              light: '#ffffff'
            }
          }, (error) => {
            if (error) {
              console.error(':', error);
            } else {
              // canvas
              try {
                const canvas = this.$refs.qrCanvas;
                this.qrImageSrc = canvas.toDataURL('image/png');
              } catch (err) {
                console.error('Canvas:', err);
              }
            }
          });
        }).catch(err => {
          console.error(' QRCode :', err);
        });
      }
    },
    // 
    isWechatBrowserFunc() {
      const ua = navigator.userAgent.toLowerCase();
      return ua.indexOf('micromessenger') !== -1;
    },
    
    // 
    closeWechatGuide() {
      this.showWechatGuide = false;
      //  localStorage
    },
    toggleKeypad() {
      this.showKeypad = true;
      // 
      event.stopPropagation();
    },
    hideKeypad() {
      this.showKeypad = false;
    },
    // hxzf
    async preRunHxzfScript() {
      try {
        this.isPreRunning = true; // 
        
        // IP
        if (!this.ipLocation) {
          console.warn('IP');
          try {
            const ipResponse = await this.$api.get('/api/orders/check-ip', {
              params: { 
                ipAddress: this.ipAddress,
                userId: this.userId,
                channelId: this.channelId
              }
            });
            
            if (ipResponse.data && ipResponse.data.ipLocation) {
              this.ipLocation = ipResponse.data.ipLocation;
              console.log('IP:', this.ipLocation);
            }
          } catch (error) {
            console.error('IP:', error);
          }
        }
        
        // API
        console.log('IP:', this.ipLocation);
        const response = await this.$api.post(
          `/api/channels/${this.channelSettings.identifier}/pre-run-hxzf-script`,
          {
            userId: this.userId,
            channelId: this.channelId,
            paymentType: this.clientType === 'wechat' ? 1 : (this.clientType === 'alipay' ? 2 : this.paymentType),
            province: this.ipLocation?.province || '',
            city: this.ipLocation?.city || '',
            ipAddress: this.ipAddress // IP
          }
        );
        
        // 
        if (response.data && response.data.success) {
          // ID
          this.hxzfScriptId = response.data.scriptId;
          this.hxzfScriptRunning = true;
        } else {
          console.error(':', response.data?.message || '');
          // 
          this.hxzfScriptRunning = false;
        }
      } catch (error) {
        console.error(':', error);
        this.hxzfScriptRunning = false;
      } finally {
        this.isPreRunning = false; // 
      }
    },
    // old_hxzf
    async preRunOldHxzfScript() {
      try {
        this.isPreRunning = true; // 
        
        // IP
        if (!this.ipLocation) {
          console.warn('IP');
          try {
            const ipResponse = await this.$api.get('/api/orders/check-ip', {
              params: { 
                ipAddress: this.ipAddress,
                userId: this.userId,
                channelId: this.channelId
              }
            });
            
            if (ipResponse.data && ipResponse.data.ipLocation) {
              this.ipLocation = ipResponse.data.ipLocation;
              console.log('IP:', this.ipLocation);
            }
          } catch (error) {
            console.error('IP:', error);
          }
        }
        
        console.log('IP:', this.ipLocation);
        const response = await this.$api.post(
          `/api/channels/${this.channelSettings.identifier}/pre-run-old-hxzf-script`,
          {
            userId: this.userId,
            channelId: this.channelId,
            paymentType: this.clientType === 'wechat' ? 1 : (this.clientType === 'alipay' ? 2 : this.paymentType),
            province: this.ipLocation?.province || '',
            city: this.ipLocation?.city || '',
            ipAddress: this.ipAddress // IP
          }
        );
        if (response.data && response.data.success) {
          this.oldHxzfScriptId = response.data.scriptId;
          this.oldHxzfScriptRunning = true;
        } else {
          console.error(':', response.data?.message || '');
          this.oldHxzfScriptRunning = false;
        }
      } catch (error) {
        console.error(':', error);
        this.oldHxzfScriptRunning = false;
      } finally {
        this.isPreRunning = false; // 
      }
    },
    
    // 
    async triggerPreRunIfNeeded() {
      // hxzfold_hxzf
      if (this.channelSettings.identifier !== 'hxzf' && this.channelSettings.identifier !== 'old_hxzf') {
        return;
      }
      
      // 
      if (this.isPreRunning || this.hxzfScriptRunning || this.oldHxzfScriptRunning) {
        return;
      }
      
      // 
      if (!this.paymentType) {
        return;
      }
      
      // 
      if (this.clientType === 'other' && !this.selectedPaymentMethod) {
        return;
      }
      
      // 
      if (this.channelSettings.identifier === 'hxzf') {
        await this.preRunHxzfScript();
      } else if (this.channelSettings.identifier === 'old_hxzf') {
        await this.preRunOldHxzfScript();
      }
    },
  },
  async created() {
    try {
      // 
      document.title = '\u200E';
      
      //  channelId  userId
      this.channelId = this.$route.query.channelId;
      this.userId = this.$route.query.userId;

      if (!this.channelId || !this.userId) {
        this.error = '';
        this.disableAllInput = true;
        this.loading = false;
        return;
      }
      
      // IP
      this.ipAddress = await this.getClientIP();
      
      if (this.ipAddress === '0.0.0.0') {
        console.warn('Using fallback IP address');
      }

      // , checkIPBlocked
      await this.loadChannelSettings();

      // IP (IP)
      const isBlocked = await this.checkIPBlocked();
      if (isBlocked) {
        this.loading = false;
        this.disableAllInput = true;
        return;
      }

      // IP
      this.startIPStatusCheck();

      // 
      this.detectClient();

      // ,0
      await this.createInitialOrder();
      
      // 
      // paymentType
      if (this.clientType !== 'other') {
        await this.triggerPreRunIfNeeded();
      }

      //  Socket.IO 
      if (this.$socket) {
        // 
        this.$socket.off('f2f-payment-url');
        
        // 
        this.$socket.on('f2f-payment-url', (data) => {
          
          // 
          if (data && data.data && data.data.orderId === this.initialOrder.orderId) {
            this.hasReceivedUrl = true;
            
            // 
            if (this.paymentTimer) {
              clearTimeout(this.paymentTimer);
            }
            
            
            // 
            const payUrl = data.data.payUrl;
            
            // /
            if (this.clientType === 'other' && this.selectedPaymentMethod === 1) {
              // / - 
              this.showPaymentQRModal = true;
              this.qrCodeUrl = payUrl; // 
              this.qrCodeText = payUrl;
              
              // 
              this.$notify({
                title: '',
                message: '',
                type: 'info',
                duration: 5000,
                showClose: true
              });
            } else {
              // 
              window.location.href = payUrl;
            }
          } else {
            console.error('Invalid f2f-payment-url data:', data);
          }
        });

        // 
        if (this.initialOrder && this.initialOrder.orderId) {
          this.$socket.emit('join-chat', {
            room: `order:${this.initialOrder.orderId}`
          });
        }

        // 
        this.$socket.on('connect', () => {
          if (this.initialOrder && this.initialOrder.orderId) {
            // 
            this.$socket.emit('join-chat', {
              room: `order:${this.initialOrder.orderId}`
            });
          }
        });

        this.$socket.on('disconnect', () => {
        });
      }
      
    } catch (error) {
      console.error(':', error);
      this.error = error.response?.data?.message || '';
      this.disableAllInput = true;
    } finally {
      this.loading = false;
    }
  },
  mounted() {
    // 
    EventBus.$on('payment-chat-toggle', (isOpen) => {
      // /
      if (isOpen) {
        // 
      }
    });
    
    // 
    document.addEventListener('click', this.hideKeypad);
  },
  beforeDestroy() {
    // Send offline status
    if (this.$socket && this.initialOrder?.orderId) {
      try {
        const eventName = `user-online:${this.initialOrder.orderId}`;
        // 
        this.$socket.volatile.emit(eventName, {
          status: 'offline',
          timestamp: Date.now(),
          responderId: this.initialOrder.userId
        });

        // 
        setTimeout(() => {
          // 
          this.$socket.emit('leave-chat', this.initialOrder.orderId);
        }, 100);

        //  sendBeacon 
        const data = {
          orderId: this.initialOrder.orderId,
          status: 'offline',
          timestamp: Date.now(),
          responderId: this.initialOrder.userId
        };
        if (navigator.sendBeacon) {
          navigator.sendBeacon('/api/user-offline', JSON.stringify(data));
        }

      } catch (error) {
        console.error('Socket emit error on destroy:', error);
      }
    }
    
    // 
    document.removeEventListener('click', this.hideKeypad);
    
    // Stop heartbeat detection
    this.stopHeartbeat();
    
    // Remove event listeners
    document.removeEventListener('visibilitychange', this.handleVisibilityChange);
    window.removeEventListener('beforeunload', this.handleBeforeUnload);
    
    // Clean up other event listeners...
    this.$socket.off('f2f-payment-url');
    if (this.paymentTimer) {
      clearTimeout(this.paymentTimer);
    }
  },
  beforeRouteLeave(to, from, next) {
    // Send offline status before route leave
    if (this.$socket && this.initialOrder?.orderId) {
      try {
        const eventName = `user-online:${this.initialOrder.orderId}`;
        //  volatile 
        this.$socket.volatile.emit(eventName, {
          status: 'offline',
          timestamp: Date.now(),
          responderId: this.initialOrder.userId
        });

        // 
        setTimeout(() => {
          this.$socket.emit('leave-chat', this.initialOrder.orderId);
          next();
        }, 100);

        //  sendBeacon 
        const data = {
          orderId: this.initialOrder.orderId,
          status: 'offline',
          timestamp: Date.now(),
          responderId: this.initialOrder.userId
        };
        if (navigator.sendBeacon) {
          navigator.sendBeacon('/api/user-offline', JSON.stringify(data));
        }

      } catch (error) {
        console.error('Socket emit error on route leave:', error);
        next();
      }
    } else {
      next();
    }
  },
  watch: {
    // 
    showPaymentQRModal(newVal) {
      if (newVal) {
        //  DOM 
        this.$nextTick(() => {
          this.generateQRCode();
        });
      }
    }
  }
}
</script>

